@charset "UTF-8";
@import '../utils/variables';
@import '../utils/mixins';
@import '../utils/reset';

body {
    background: $liLight;
    overflow: auto;
}

@include form_fields;

@import '../utils/button';
@import '../utils/message';
@import '../utils/dialog';
@import '../utils/modal';
@import 'dashboard';
@import 'fonts';
@import 'mytemplates';
@import 'settings';

/* Responsive */

@media screen and ( max-width: 650px ) {

    .comet-dashboard.comet-admin .comet-content.comet-boxed > .comet-row.comet-wrapper > .comet-column{ width: 100%; }
}

@media screen and ( max-width: 500px ) {

    .comet-settings.comet-dashboard .comet-content.comet-boxed .comet-setting.comet-row .comet-settingBlock.comet-column {
        width: 100%;
        padding: 0;

        &.last { padding-top: 10px }

    }

}
